.product-item{
  padding: 4.38em 0 1.9375em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 53em - 3.125em) 53em;
    gap: 3.125em;
  }
}

.gallery{
  display: grid;
  grid-template-columns: 6.6875em calc(100% - 6.6875em - 1.56em);
  gap: 1.56em;
  &__img{
    display: block;
    @include imgCover(76.2%);
    padding: 0;
    height: 100%;
    background-color: #FBFBFB;
    img{
      transition: all .3s;
    }
  }
  &__small{
    position: relative;
    height: 38.5em;
    .swiper{
      height: calc(100% + 1.56em);
      &-slide{
        height: 20%;
        padding-bottom: 1.56em;
        position: relative;
        &:hover{
          cursor: pointer;
        }
        &:hover,
        &-thumb-active{
          .gallery__img:before{
            opacity: 1;
          }
        }
      }
      &-button{
        position: absolute;
        width: .9375em;
        z-index: 3;
        left: 50%;
        top: unset;
        bottom: 0;
        margin: 0;
        background-color: transparent;
        svg{
          width: 100%;
          height: auto;
        }
        &:hover{
          background-color: transparent;
          path{
            fill: #2F343A;
            opacity: 1;
          }
        }
      }
    }
    .gallery__img{
      padding: 0;
      height: 100%;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: inherit;
        border: 1px solid $black;
        z-index: 2;
        transition: all .3s;
        opacity: 0;
      }
    }
  }
  &__big{
    width: 100%;
    position: relative;
    height: 38.5em;
    .swiper{
      height: 100%;
    }
  }
  &__nav,
  &__zoom{
    bottom: 2.5em;
    position: absolute;
    z-index: 2;
  }
  &__nav{
    display: flex;
    align-items: center;
    right: 2.5em;
    height: 1.375em;
    .swiper-button{
      position: static;
      width: .5625em;
      height: auto;
      svg{
        width: 100%;
        height: auto;
      }
      &-next{
        margin-left: 1.25em;
      }
    }
  }
  &__zoom{
    left: 2.5em;
    width: 1.375em;
    pointer-events: none;
    img{
      width: 100%;
      height: auto;
    }
  }
  &__3d{
    position: absolute;
    right: 1.56em;
    top: 1.56em;
    padding: 0 .72em;
    z-index: 2;
    height: 3.1875em;
    span{
      font-size: .9375em;
    }
    svg{
      width: 1.9375em;
      margin-right: .75em;
    }
    &:hover{
      background-color: $black;
      color: #fff;
      path{
        stroke: #fff;
        fill: none;
      }
    }
  }
}

.product-info{
  padding-top: .375em;
  &__top{
    position: relative;
    padding-right: 4.5em;
    @include grid100gap(.875em);
  }
  &__favourite{
    position: absolute;
    top: .5em;
    right: 0;
    width: 4.0625em;
    height: 4.0625em;
    @include center;
    .favourite-btn{
      &:before{
        content: '';
        @include centerAbsolute;
        width: 4.0625em;
        height: 4.0625em;
        border-radius: 50%;
        background-color: #FAFAFA;
        z-index: -1;
      }
    }
  }
  &__colors{
    margin-top: 2.19em;
    @include grid100gap(.69em);
  }
  .catalog-item__color{
    padding: 0;
  }
  .color-field{
    min-height: 2.1875em;
    margin-top: .5em;
    &__item{
      width: 2.40788em;
      height: 1.83em;
    }
  }
  .catalog-item__hint{
    margin-top: 2.94em;
  }
  .product-card__prices{
    margin-top: 2.25em;
    display: flex;
    align-items: center;
  }
  .product-card__sale{
    margin: 0 0 0 1.06em;
  }
  &__nav{
    display: flex;
    margin-top: 1.75em;
  }
}

.catalog-item-collection{
  padding: 2.5em 0;
}


@media screen and (max-width: 992px){
  .product-item{
    padding: 2.5em 0 1.25em;
    &__inner{
      grid-template-columns: 20.625em calc(100% - 20.625em - 1.56em);
      gap: 1.56em;
    }
  }

  .gallery{
    grid-template-columns: 100%;
    gap: .94em;
    &__big{
      height: 18.28em;
    }
    &__small{
      height: 6em;
      order: 2;
      .swiper{
        height: 100%;
        width: calc(100% + .94em);
        &-slide{
          height: 100%;
          width: calc(100% / 3);
          padding: 0 .94em 0 0;
        }
      }
    }
    &__3d{
      top: .625em;
      right: .625em;
      height: 2.375em;
      padding: 0 .88em;
      span{
        font-size: .875em;
      }
      svg{
        width: 1.75em;
      }
    }
    &__zoom{
      width: 1em;
      bottom: .625em;
      left: .625em;
    }
    &__nav{
      right: .625em;
      bottom: .625em;
      height: 1em;
      .swiper-button{
        width: .4375em;
      }
    }
  }

  .product-info{
    &__top{
      padding: 0;
      gap: .625em;
    }
    &__favourite{
      display: none;
    }
    &__colors{
      margin-top: 1.25em;
    }
    .product-card__prices{
      margin-top: 1.125em;
    }
    &__nav{
      margin-top: 1.125em;
      .m-btn{
        width: 100%;
        max-width: 20.625em;
      }
    }
  }

  .catalog-item-collection{
    padding: 2.875em 0;
  }
}


@media screen and (max-width: 576px) {
  .product-item {
    padding: 1.25em 0 .625em;
    &__inner {
      grid-template-columns: 100%;
    }
  }

  .product-info{
    &__top{
      margin-bottom: 1.25em;
    }
    &__colors{
      margin-top: 0;
    }
    &__nav{
      .m-btn{
        max-width: 100%;
      }
    }
  }

  .catalog-item-collection{
    padding: 1.25em 0;
  }
}
