.about{
  padding: 6.25em 0 2.5em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 40.0625em - 6.69em) 40.0625em;
    justify-content: space-between;
  }
  &__title{
    span{
      display: block;
      line-height: inherit;
      padding: .26em 0 0 1.94em;
    }
  }
  &__wrap{
    padding: 2.62em 0 0 6.8em;
    display: flex;
    position: relative;
  }
  &__counts{
    flex-shrink: 0;
    margin-right: 3.5em;
    .about-count{
      @include lastM(0 0 2.5em);
    }
  }
  &__subtitle{
    position: absolute;
    left: 0;
    bottom: 4.12em;
    opacity: 0.1;
    font-weight: 800;
    text-transform: uppercase;
    font-family: $font2;
    display: flex;
    flex-direction: column;
    align-items: center;
    &:before{
      content: '';
      width: 0.0625em;
      height: 3.375em;
      flex-shrink: 0;
      background: #000;
      margin-bottom: 1.44em;
    }
    .text15{
      writing-mode: tb-rl;
      transform: rotate(-180deg);
    }
  }
  &__nav{
    margin-top: 2.06em;
    display: flex;
  }
  &__img{
    position: relative;
    z-index: 2;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.about-count{
  width: 15.9375em;
  height: 15.9375em;
  border-radius: 50%;
  background-color: $primary;
  color: #fff;
  padding: 4.25em 4em 3.6em;
  &._gray{
    background-color: #FBFBFB;
    color: $black;
  }
  &._type2{
    position: absolute;
    width: 19.375em;
    height: 19.375em;
    padding: 5em 1em 4em 4em;
    right: -9.38em;
    bottom: -1em;
    border: 1px solid #C5C6C6;
    background-color: transparent;
    color: $black;
  }
  &__desc{
    .text18{
      letter-spacing: -0.03375em;
    }
  }
  &__top{
    display: flex;
    align-items: flex-end;
    white-space: nowrap;
    position: relative;
    font-weight: 100;
    font-family: $font2;
    .text31{
      margin-left: .3125em;
      transform: translateY(-35%);
    }
  }
  .text76{
    font-weight: 100;
  }
  &__compare{
    position: absolute;
    top: 50%;
    right: 100%;
    transform: translateY(-50%);
    margin-right: .25em;
  }
}

.about-info{
  @include grid100gap(.94em);
  &:not(:first-child){
    margin-top: 1.63em;
  }
  .text23{
    max-width: 17em;
  }
  .text19{
    color: #666;
  }
  @media screen and (min-width: 993px){
    &._small{
      max-width: 23.5625em;
    }
  }
}

.blog{
  padding: 2.5em 0 3.125em;
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.blog-slider{
  margin-top: 2.94em;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 2.5em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: calc(100% / 3);
      padding-right: 2.5em;
      height: auto;
    }
    &-nav{
      display: none;
    }
  }
}

.blog-card{
  display: flex;
  flex-direction: column;
  height: 100%;
  &__img{
    display: block;
    @include imgCover(75.5%);
  }
  &__content{
    margin-top: .94em;
    @include grid100gap(.375em)
  }
  &__info{
    display: flex;
    align-items: center;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 3.75em;
      height: 0.0625em;
      background-color: #C5C6C6;
      opacity: .6;
    }
    &-split{
      margin: 0 .875em;
      color: #7D7D7D;
      .text13{
        line-height: 284%;
      }
    }
  }
  &__type{
    font-weight: 500;
    text-transform: uppercase;
    color: #3E3E3E;
  }
  &__desc{
    @include truncate(2);
    color: #4D4D4D;
  }
  &__nav{
    padding-top: .375em;
    margin-top: auto;
    display: flex;
  }
}

.living-room{
  padding: 3.125em 0 6.25em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 34.0625em - 6.25em) 34.0625em;
    justify-content: space-between;
    align-items: center;
  }
  &__content{
    padding-left: 6.25em;
    display: grid;
    grid-template-columns: 34.125em calc(100% - 34.125em - 2.56em);
    gap: 2.56em;
    align-items: center;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    margin-left: -5.81em;
    .text46{
      letter-spacing: -0.03em;
      line-height: 110%;
    }
  }
  &__text{
    margin-top: 1.5em;
    color: $darkGray;
    p{
      font-size: 1.125em;
      line-height: 167%;
      @include lastM(0 0 .61em);
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.63em;
  }
}

.product-slider2{
  position: relative;
  padding-right: 3.75em;
  &__wrap{
    overflow: hidden;
    height: 39.8125em;
    border: 1px solid #CECECE;
  }
  .swiper{
    &-container{
      height: calc(100% + 2px);
      margin-top: -1px;
    }
    &-slide{
      height: 25%;
      &:first-child{
        .product-card2{
          border-top: 1px solid #CECECE;
        }
      }
      .product-card2{
        border-bottom: 1px solid #CECECE;
      }
    }
    &-nav{
      @media screen and (min-width: 993px){
        position: absolute;
        right: 0;
        top: 50%;
        transform: translate(50%,-50%) rotate(90deg);
      }
    }
  }
}

.product-card2{
  display: grid;
  grid-template-columns: 10.75em calc(100% - 10.75em);
  align-items: center;
  &__left{
    position: relative;
    border-right: 1px solid #CECECE;
    background-color: #FBFBFB;
  }
  &__img{
    display: block;
    @include imgCover(91.8%);
  }
  .favourite-btn{
    position: absolute;
    top: 1.21em;
    left: 1.21em;
    z-index: 2;
  }
  &__content{
    padding: 1em 1.875em 1em 1em;
  }
  &__title{
    @include truncate(1);
  }
  .product-card__reviews{
    margin: 0 0 .625em;
  }
  &__bottom{
    margin-top: .625em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.cart-btn{
  width: 2.75em;
  height: 2.75em;
  background: #F8F8F8;
  svg{
    width: .9375em;
    height: auto;
  }
  &:hover{
    background: $primary;
    path{
      stroke: #fff;
    }
  }
  &._added{
    background: $primary;
    svg{
      opacity: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .about{
    padding: 3.125em 0 1.5625em;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.875em;
    }
    .about-count._type2,
    &__subtitle{
      display: none;
    }
    &__title{
      span{
        display: inline;
        padding: 0;
      }
    }
    &__wrap{
      padding: 1.69em 0 0;
    }
    &__counts{
      margin-right: 1.625em;
      .about-count{
        @include lastM(0 0 .94em);
      }
    }
    &__nav{
      margin-top: .875em;
    }
    &__img{
      @media screen and (min-width: 577px){
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
  }

  .about-count{
    padding: 1.94em .875em 2em 2.8em;
    width: 9.875em;
    height: 9.875em;
    .text76{
      font-size: 2.9375em;
    }
    &__top{
      .text31{
        font-size: 1em;
      }
    }
    &__desc{
      .text18{
        font-size: .9375em;
        line-height: 115%;
      }
    }
    &__compare{
      margin-right: .51em;
      .text46{
        font-size: 2.0625em;
      }
    }
  }

  .about-info{
    gap: .19em;
    &:not(:first-child){
      margin-top: .875em;
    }
    .text23{
      font-size: 1.125em;
      max-width: 100%;
    }
    .text19{
      font-size: 1.0625em;
    }
  }

  .blog{
    padding: 1.5625em 0;
    &__inner{
      position: relative;
      padding-bottom: 5.25em;
    }
    &__nav{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      display: flex;
      justify-content: center;
    }
  }

  .blog-slider{
    margin-top: 1.56em;
    position: relative;
    .swiper{
      &-container{
        width: calc(100% + 1.06em);
      }
      &-slide{
        padding-right: 1.06em;
      }
      &-nav{
        display: flex;
        margin-bottom: 1.875em;
        right: 0;
        bottom: 100%;
        position: absolute;
      }
    }
  }

  .blog-card{
    &__content{
      gap: 0;
      margin-top: .625em;
    }
    &__title{
      font-size: 1em;
      line-height: 138%;
    }
    &__info{
      display: block;
      margin-top: .3125em;
      &:before{
        display: none;
      }
      &-split{
        display: none;
      }
    }
    &__type{
      line-height: 180%;
    }
    &__date{
      line-height: 180%;
    }
    &__desc{
      margin-top: .3125em;
      -webkit-line-clamp: 4;
      font-size: 0.8125em;
      line-height: 161%;
    }
    &__nav{
      padding-top: .12em;
    }
  }

  .living-room{
    padding: 1.5625em 0 1.875em;
    &__inner{
      grid-template-columns: 20.75em calc(100% - 20.75em - 1.625em);
      gap: 1.31em 1.625em;
    }
    &__content{
      padding: 0;
      grid-template-columns: 100%;
    }
    &__img{
      display: none;
    }
    &__title{
      margin: 0;
      .text46{
        line-height: 107%;
        font-weight: 400;
      }
    }
    &__text{
      margin-top: 1.25em;
      p{
        font-size: 1em;
        margin: 0 !important;
      }
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .product-slider2{
    padding-right: 0;
    &__wrap{
      height: 28.5em;
    }
    .swiper-nav{
      margin-top: 1.5625em;
      justify-content: center;
    }
  }

  .product-card2{
    grid-template-columns: 7.75em calc(100% - 7.75em);
    &__content{
      padding: .25em .94em;
    }
    .product-card__reviews{
      margin-bottom: .5em;
    }
    .text21{
      line-height: 111%;
    }
    &__bottom{
      margin-top: .5em;
    }
    .favourite-btn{
      top: .4em;
      left: .4em;
    }
  }

  .cart-btn{
    height: 2em;
    width: 2em;
  }
}


@media screen and (max-width: 576px){
  .about{
    padding: 2.5em 0 1.25em;
    &__inner{
      gap: 1.6875em;
      position: relative;
      padding-bottom: 5.5625em;
    }
    &__wrap{
      padding-top: 1em;
      flex-direction: column;
      position: static;
    }
    &__counts{
      order: 2;
      display: flex;
      margin: 1.25em 0 0;
      .about-count{
        @include lastM(0 .94em 0 0);
      }
    }
    &__nav{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      justify-content: center;
    }
  }

  .blog{
    overflow: hidden;
  }

  .blog-slider{
    &__wrap{
      overflow: unset;
    }
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        padding-right: .94em;
        width: 18.6275em;
      }
    }
  }

  .living-room{
    padding: 1.25em 0 2.5em;
    &__inner{
      grid-template-columns: 100%;
      position: relative;
      padding-bottom: 5em;
    }
    &__nav{
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      justify-content: center;
    }
    &__products{
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
    }
  }
}
