.footer{
  &__logo{
    display: block;
    width: 9.1875em;
    max-width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 1.0625em;
    line-height: 117%;
    font-weight: 700;
    text-transform: uppercase;
  }
}


.footer-top{
  padding: 3em 0;
  border-top: 1px solid rgba(#C5C6C6,.3);
  &__inner{
    display: flex;
    justify-content: space-between;
  }
}

.footer-info,
.subscribe-socials{
  .socials{
    margin-top: .13em;
    &__item{
      width: 2.9375em;
      height: 3.125em;
      img{
        max-width: 1.5em;
        max-height: 1em;
      }
    }
  }
}

.footer-info{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__socials{
    margin: 1.875em 0;
    .text15{
      line-height: 200%;
    }
  }
  &__by{
    margin-top: auto;
    width: 8.75em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-nav{
  &__bottom{
    margin-top: 1.19em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    color: #636565;
    @include lastM(0 0 1.19em);
    span{
      font-size: 1.0625em;
      line-height: 118%;
    }
  }
}

.footer-contacts{
  min-width: 22em;
  &__inner{
    margin-top: 1.19em;
    @include grid100gap(1.19em);
  }
  &__phones{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__phone{
    display: flex;
    align-items: center;
    color: #636565;
    font-weight: 500;
    &:hover{
      path{
        stroke: $primary;
      }
    }
    span{
      font-size: 1.125em;
      line-height: 167%;
    }
    img{
      width: 1.375em;
      height: auto;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
  &__messengers{
    display: flex;
    padding-left: 2.12em;
    margin-top: .44em;
  }
  &__messenger{
    color: #4E4E4E;
    font-weight: 500;
    position: relative;
    padding-left: .5625em;
    @include lastM(0 .875em 0 0);
    &:before{
      content: '';
      position: absolute;
      width: 0.3125em;
      height: 0.3125em;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      background-color: $primary;
      border-radius: 50%;
    }
    span{
      font-size: .8125em;
      line-height: 115%;
      border-bottom: 1px solid rgba(#4E4E4E,.2);
    }
  }
  &__email,
  &__address{
    display: flex;
    align-items: center;
    color: #636565;
    span{
      font-size: 1em;
      line-height: 187%;
    }
    img{
      width: 1.25em;
      flex-shrink: 0;
      margin-right: .75em;
    }
  }
}

.footer-payment{
  &__title{
    font-size: 1em;
    line-height: 188%;
    font-weight: 700;
    color: #424242;
  }
  &__items{
    display: flex;
    align-items: center;
    margin-top: 1.19em;
  }
  &__item{
    display: flex;
    align-items: center;
    transition: all .3s;
    @include lastM(0 1em 0 0);
    &:not(:hover){
      filter: grayscale(1);
      opacity: .4;
    }
    img{
      max-height: 1.25em;
      max-width: 4em;
      flex-grow: 1;
      display: block;
    }
  }
}

.footer-bottom{
  color: $darkGray;
  border-top: 1px solid rgba(#C5C6C6,.3);
  &__inner{
    @include center;
    min-height: 4.3125em;
    padding: .94em 0;
  }
}


@media screen and (max-width: 992px){
  .footer{
    position: relative;
    &__logo{
      width: 7em;
    }
    &__title{
      font-size: 1em;
      line-height: 125%;
    }
  }

  .footer-top{
    padding: 1.625em 0 1.5em;
    &__inner{
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3));
      gap: 1.5em 0;
    }
  }

  .footer-info{
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    grid-column: 1/-1;
    &__by{
      position: absolute;
      right: 1.5625em;
      bottom: .94em;
    }
    &__socials{
      margin: 0;
      display: flex;
      align-items: center;
      .socials{
        margin: 0 0 0 1.25em;
      }
    }
  }

  .footer-nav{
    &__bottom{
      margin-top: .875em;
    }
    &__link{
      @include lastM(0 0 .875em);
      span{
        font-size: 1em;
        line-height: 125%;
      }
    }
  }

  .footer-contacts{
    grid-column: 1/-1;
    .footer__title{
      text-align: center;
    }
    &__inner{
      margin-top: .75em;
      grid-template-columns: repeat(3,calc(100% / 3));
      gap: 1.5em 0;
      align-items: flex-start;
    }
    &__address{
      align-items: flex-start;
      img{
        margin-top: .25em;
      }
    }
    &__phone{
      img{
        width: 1.0625em;
      }
      span{
        font-size: 1em;
        line-height: 187%;
      }
    }
    &__messengers{
      margin-top: .12em;
    }
  }

  .footer-payment{
    grid-column: 1/-1;
    &__title{
      text-align: center;
    }
    &__items{
      margin-top: .625em;
      justify-content: space-between;
    }
  }

  .footer-bottom{
    &__inner{
      padding-right: 10em;
      min-height: 3.375em;
      justify-content: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .footer-top{
    padding-bottom: 3.44em;
    position: relative;
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
  }

  .footer-info{
    margin-bottom: 1em;
    &__socials{
      .text15{
        display: none;
      }
    }
    &__by{
      right: 50%;
      transform: translateX(50%);
      bottom: 1.19em;
    }
  }

  .footer-nav{
    &._active{
      .footer-nav__top:after{
        transform: rotate(-180deg);
      }
    }
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
      min-height: 2.75em;
      border-bottom: 1px solid #E8E8E8;
      padding: .31em;
      &:after{
        content: '';
        flex-shrink: 0;
        background-image: url(../img/arrow-down.svg);
        @include bgImgSize(100%);
        width: .5em;
        height: .5em;
        margin-left: 1em;
        transition: all .3s;
      }
    }
    &__bottom{
      display: none;
      margin: 0;
      padding: .625em .94em;
    }
  }

  .footer-contacts{
    margin-top: 1.56em;
    min-width: unset;
    &__inner{
      margin-top: .875em;
      grid-template-columns: 100%;
      gap: .625em;
    }
    &__phones{
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    &__phone{
      max-width: 50%;
    }
    &__messengers{
      width: 100%;
      margin-top: 1.06em;
      padding: 0;
      justify-content: center;
    }
  }

  .footer-bottom{
    &__inner{
      padding-right: 0;
      justify-content: center;
      text-align: center;
    }
  }
}
