@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.25em;
    height: 5.0625em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.25em;
        line-height: 135%;
    }
    svg{
        width: .5625em;
        height: auto;
        margin-left: .625em;
    }
    &._small{
        padding: 0 1.69em;
        height: 3.8125em;
        span{
            font-size: 1.125em;
        }
    }
}

.m-btn-white{
    background-color: #FFF;
    color: $black;
    &:hover{
        background-color: #F9F9F9;
        color: $primary;
        path{
            fill: $primary;
        }
    }
}


.m-btn-red{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: #B71015;
        color: #fff;
    }
}

.m-btn-black{
    background-color: $black;
    color: #fff;
    &:hover{
        background-color: $darkGray;
        color: rgba(#fff,.7);
        svg{
            opacity: .7;
        }
    }
}


.m-btn-red-outline{
    border: 1px solid $primary;
    color: $black;
    &:hover{
        background-color: $primary;
        color: #fff;
        path{
            fill: #fff;
        }
    }
}


.m-btn-black-outline{
    border: 1px solid $black;
    color: $black;
    &:hover{
        background-color: $black;
        color: #fff;
        path{
            fill: #fff;
        }
    }
}

.arrow-btn{
    height: 2.8125em;
    @include center;
    color: $primary;
    font-weight: 500;
    span{
        text-decoration: underline;
        text-decoration-color: transparent;
    }
    svg{
        width: .6875em;
        height: auto;
        margin-left: .625em;
    }
    &:hover{
        span{
            text-decoration-color: $primary;
        }
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3.6875em;
        padding: 0 1.31em;
        span{
            font-size: 1em;
        }
        &._small{
            padding: 0 1.125em;
            height: 3.25em;
            span{
                font-size: 1em;
            }
        }
    }

    .arrow-btn{
        height: 1.9375em;
        span{
            font-size: .9375em;
        }
    }
}
