.rating-block{
  display: flex;
  &._disabled{
    pointer-events: none;
  }
}

.rating{
  cursor: pointer;
  &__stars{
    width: 5.5em;
    max-width: 100%;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
  }
  &__fill{
    width: 0;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    overflow: hidden;
    img{
      width: 5.5em;
      display: block;
    }
  }
}

.color-field__item{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.fg {
  position: relative;
  margin: 0;
  input {
    border: none;
    border-bottom: 1px solid #CACACA;
    padding: 0 .625em;
    height: 3.75em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: #575757;
    }
    &:focus {
      &::placeholder {
        border-color: $black;
      }
    }
  }

  .nice-select{
    min-width: 100%;
    width: auto;
    font-size: 1em;
    padding: 0 1.25em;
    border: 1px solid #F3F3F3;
    height: 2.375em;
    border-radius: 0;
    display: flex;
    align-items: center;
    &:focus:not(.open){
      border-color: $black;
    }
    .current{
      width: 100%;
      color: #808080;
      font-size: 1em;
      line-height: 125%;
      padding-right: 1em;
      background-image: url(../img/arrow-down.svg);
      background-repeat: no-repeat;
      background-position: right center;
      background-size: .5em auto;
    }
    .list{
      border: 1px solid #F3F3F3;
      border-top: none;
      margin-top: 0;
      border-radius: 0;
      background-color: #fff;
      box-shadow: none;
      width: calc(100% + 2px);
      margin-left: -1px;
    }
    .option{
      font-size: 1em;
      line-height: 125%;
      height: 2.5em;
      padding: 0 1.25em;
      color: #808080;
      display: flex;
      align-items: center;
      border: none !important;
      &.selected{
        font-weight: 400 !important;
        background-color: $black;
        color: #fff;
      }
    }
    &.open{
      border-bottom: transparent;
    }
  }
}


.color-check,
.m-radio,
.file-input,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-top: .1875em;
    span{
      font-size: 1em;
      line-height: 125%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      transition: all .100s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
  &._error{
    input:not(:checked){
      &~label:before{
        border-color: #FF1515;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $black;
        border-color: $black;
        background-size: .8125em auto;
      }
    }
  }
  label {
    padding-left: 2.375em;
    min-height: 1.625em;
    &:before {
      border: 1px solid $black;
      width: 1.625em;
      height: 1.625em;
      @include bgImgSize(0);
      background-image: url(../img/check2.svg);
      transition: 0s;
    }
  }
}
