.catalog{
  padding: 4em 0;
  &__inner{
    display: grid;
    grid-template-columns: 25em calc(100% - 27.5em);
    gap: 2.5em;
  }
  &__sidebar{
    position: relative;
    z-index: 3;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
    margin-top: 3.19em;
    @media screen and (min-width: 993px){
      .catalog-item{
        margin-top: -1px;
        margin-left: -1px;
        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3){
          margin-top: 0;
        }
        &:nth-child(3n-2){
          margin-left: 0;
          .catalog-item__dropdown{
            width: calc(100% + 5em + 3px);
          }
        }
      }
    }
  }
  .catalog-banner{
    margin-top: 2.5em;
  }
}

.catalog-top{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__show{
    color: #686868;
  }
}

.sort{
  display: flex;
  align-items: center;
  &__title{
    color: #313131;
  }
  .fg{
    margin-left: .875em;
    min-width: 13.75em;
  }
}

.selected-filters{
  display: flex;
  flex-wrap: wrap;
  width: calc(100% + .625em);
  .selected-item{
    margin: .625em .625em 0 0;
  }
}

.selected-item{
  display: flex;
  align-items: center;
  padding: 0 .94em;
  height: 2.625em;
  background: #FAFAFA;
  &._all{
    background: #EDEDED;
    font-weight: 500;
    &:hover{
      background: $black;
      color: #fff;
    }
  }
  span{
    font-size: .9375em;
    line-height: 110%;
  }
  &__remove{
    margin-left: .625em;
    width: .5625em;
    flex-shrink: 0;
    @include center;
    svg{
      width: 100%;
      height: 100%;
    }
    &:hover{
      path{
        opacity: 1;
        stroke: $primary;
      }
    }
  }
}

.menu-btn{
  display: none;
}

.catalog-item{
  border: 1px solid #D4D4D4;
  transition: all .3s;
  position: relative;
  &:hover{
    @media screen and (min-width: 993px){
      background-color: #fff;
      z-index: 3;
      border-color: #737373;
      .catalog-item__dropdown{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  .product-card{
    border: none;
  }
  &__top{
    position: relative;
    @include grid100gap(.75em)
  }
  &__dropdown{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: absolute;
    left: calc(-2.5em - 1px);
    top: 100%;
    background-color: #fff;
    z-index: 2;
    width: calc(100% + 5em + 2px);
    padding: .75em 2.5em 2.5em;
    border: 1px solid #737373;
    border-top: none;
    .product-card__bottom{
      margin-top: .75em;
    }
  }
  &__color{
    padding: .31em 0;
    margin-bottom: .19em;
    .color-field{
      margin-top: .13em;
      min-height: 1.6875em;
      &__item{
        width: 1.75em;
        height: 1.33em;
        @include lastM(0 .75em 0 0);
      }
    }
  }
  &__props{
    margin-top: .75em;
    @include grid100gap(.375em);
  }
  &__hint{
    padding: 1.12em 1.25em;
    background: #F8F8F8;
    .text15{
      line-height: 133%;
    }
    a{
      text-decoration: underline;
    }
  }
}

.prop-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:after{
    content: '';
    flex-grow: 1;
    height: 1px;
    min-width: .125em;
    border-top: 1px dashed #DDD;
    margin: 0 .94em;
  }
  &__desc{
    color: #848484;
    order: 2;
  }
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 181%;
  }
  &._type2{
    .prop-item__title{
      font-weight: 500;
    }
    .prop-item__desc{
      font-size: 1.375em;
      color: #003B73;
      line-height: 100%;
    }
  }
}

.pagination{
  margin-top: 5em;
  &__items{
    display: flex;
    justify-content: center;
  }
  &__item{
    height: 3.625em;
    min-width: 3.625em;
    padding: 0 1.25em;
    border: 1px solid #E9EAEE;
    @include center;
    margin: 0 .3125em;
    font-weight: 500;
    svg{
      width: 1.4375em;
      height: auto;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $black;
      border-color: $black;
      path{
        fill: #fff;
      }
    }
    &._btn{
      padding: 0 1.5em;
    }
  }
}

.filter{
  position: sticky;
  top: 1em;
  &__nav,
  &__bg,
  &__close{
    display: none;
  }
  &__groups{
    @include grid100gap(2.5em);
  }
}

.filter-group{
  border: 1px solid #F3F3F3;
  padding: 2.5em 3.125em;
  &__top{
    margin-bottom: 1.5em;
  }
  &__checks{
    padding: .31em .5em;
    @include grid100gap(1.125em);
    &:not(._show-all){
      .m-check:nth-child(4)~.m-check{
        display: none;
      }
      .color-check:nth-child(4)~.color-check{
        display: none;
      }
    }
    &._show-all{
      .more-checkbox__wrap{
        display: none;
      }
    }
  }
}

.filter-categories{
  display: inline-block;
  padding: 0 .375em;
  &__top{
    display: flex;
    align-items: center;
    svg{
      width: .3125em;
      height: auto;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__title{
    line-height: 155%;
  }
  &__count{
    margin-left: auto;
    padding-left: .5em;
    align-self: flex-start;
    color: #A6A6A6;
  }
  &__bottom{
    margin-top: 1.125em;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0 1.25em;
  }
  &__link{
    color: #373737;
    @include lastM(0 0 .69em);
    span{
      font-size: 1.0625em;
      line-height: 165%;
    }
    &._active{
      color: $primary;
    }
  }
}

.filter-slider{
  &__item{
    min-height: .9375em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: #E7EAEA;
    border: none;
    box-shadow: none;
    height: .125em;
    border-radius: 0;
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      border: none;
      background-color: $black;
      width: .9375em;
      height: .9375em;
      top: -.34375em !important;
      right: -.34375em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin: 0 0 1.68em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5625em));
    gap: 1.125em;
  }
  .fg{
    input{
      height: 2.5em;
      text-align: center;
      color: #575757;
    }
    span{
      position: absolute;
      top: 50%;
      left: 1em;
      transform: translateY(-50%);
      font-size: .9375em;
      line-height: 120%;
      color: #A7AAAE;
    }
  }
}

.filter-sizes{
  @include grid100gap(1.69em);
  &__item{
    @include grid100gap(.94em);
  }
  &__title{
    color: #7B7979;
    line-height: 125%;
  }
}

.more-checkbox{
  font-size: 1em;
  line-height: 125%;
  font-weight: 500;
  border-bottom: 1px dotted $black;
  &:hover{
    color: $primary;
    border-color: $primary;
  }
  &__wrap{
    display: flex;
  }
}

.color-check{
  display: flex;
  align-items: center;
  input:checked{
    &~.color-check__bg{
      border-color: $black;
    }
  }
  &__bg{
    width: 1.4375em;
    height: 1.4375em;
    padding: .125em;
    @include center;
    border: .0625em solid transparent;
    margin-right: .625em;
    transition: all .3s;
    span{
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
  }
}

.catalog-banner{
  background: #EAE5E1;
  position: relative;
  overflow: hidden;
  &:before{
    content: '';
    position: absolute;
    width: 11.125em;
    height: 44.87906em;
    right: 4.875em;
    bottom: -13.25em;
    background: rgba(255, 255, 255, 0.33);
    transform: rotate(45deg);
  }
  &__content{
    padding: 3.5em 2.875em .25em;
    position: relative;
    z-index: 2;
  }
  &__title{
    max-width: 15em;
    margin-top: .625em;
  }
  &__nav{
    display: flex;
    margin-top: 1.125em;
  }
  &__img{
    position: relative;
    z-index: 2;
    margin: 0 auto;
    max-width: 100%;
    width: 17.5625em;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog{
    padding: 1.25em 0;
    &__inner{
      grid-template-columns: 100%;
    }
    &__sidebar{
      order: 2;
    }
    .menu-btn{
      margin-top: 1.25em;
      margin-bottom: .625em;
      &:first-child{
        margin-top: 0;
      }
    }
    &__items{
      grid-template-columns: repeat(4,25%);
      margin-top: 1.25em;
      @media screen and (min-width: 577px){
        .catalog-item{
          margin-top: -1px;
          margin-left: -1px;
          &:nth-child(1),
          &:nth-child(2),
          &:nth-child(3),
          &:nth-child(4){
            margin-top: 0;
          }
          &:nth-child(4n-3){
            margin-left: 0;
          }
        }
      }
    }
    .catalog-banner{
      margin-top: 0;
    }
  }

  .sort{
    &__title{
      display: none;
    }
    .fg{
      margin: 0;
      min-width: 11em;
      .nice-select{
        padding: 0 1em;
        .option{
          padding: 0 1em;
        }
      }
    }
  }

  .selected-item{
    padding: 0 .625em;
    height: 2.25em;
    span{
      font-size: .875em;
    }
  }

  .menu-btn{
    @include center;
    height: 3.5625em;
    border-top: 1px solid #D4D4D4;
    border-bottom: 1px solid #D4D4D4;
    @include fullScreenTablet;
    text-transform: uppercase;
    color: #44413F;
    font-weight: 700;
    svg{
      width: 1.25em;
      height: auto;
      margin-right: .625em;
    }
    span{
      font-size: .8125em;
      line-height: 120%;
      letter-spacing: 0.07em;
    }
  }

  .catalog-item{
    &__hint{
      padding: .625em;
      .text15{
        font-size: .8125em;
        line-height: 146%;
      }
    }
    &__dropdown{
      display: none;
    }
  }

  .pagination{
    margin-top: 2.5em;
    ._hide-tablet{
      display: none;
    }
    ._btn{
      width: 3.625em;
      padding: 0;
    }
  }

  .filter{
    @include menu;
    z-index: 20;
    padding: 0 0 3.125em;
    &._open{
      transform: translateX(0);
      &~.filter__bg,
      &~.filter__close{
        display: block;
      }
    }
    &__groups{
      gap: 0;
    }
    &__nav{
      padding: 1.25em .94em 0;
      @include grid100gap(.94em);
    }
  }

  .filter-group{
    padding: 1.56em;
    border-top: none;
    &__top{
      margin-bottom: .94em;
    }
    .text29{
      font-size: 1.5em;
    }
  }

  .catalog-banner{
    @media screen and (min-width: 577px){
      display: flex;
      justify-content: space-between;
      padding: .625em 5em 0 0;
      &:before{
        right: 9.375em;
        bottom: -17.5em;
      }
      &__content{
        padding: 3.06em 2.19em;
      }
      &__img{
        width: 13.4375em;
        margin: 0 0 0 1em;
        align-self: flex-end;
      }
    }
    .text19{
      font-size: .9375em;
      line-height: 133%;
    }
    &__title{
      max-width: 13em;
      margin-top: .56em;
      .text36{
        font-size: 1.875em;
      }
    }
    &__nav{
      margin-top: 1.56em;
      .m-btn{
        height: 2.75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__items{
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
      grid-template-columns: repeat(2,50%);
      .catalog-item{
        margin-top: -1px;
        margin-left: -1px;
        &:nth-child(1),
        &:nth-child(2){
          margin-top: 0;
        }
        &:nth-child(2n-1){
          margin-left: 0;
        }
      }
    }
  }

  .menu-btn{
    @include fullScreenMob;
  }

  .catalog-item{
    &__hint{
      margin-top: .625em;
    }
  }

  .catalog-banner{
    &:before{
      width: 7.90056em;
      height: 31.87156em;
      right: 4.1875em;
      bottom: -8.625em;
    }
    &__content{
      padding: 3.06em 1.875em .375em;
    }
    &__img{
      width: 13.125em;
    }
  }
}
