.filter-categories2{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  .filter-categories__link{
    font-weight: 500;
    @include lastM(0 0 1.69em);
  }
}

.wholesale{
  @include grid100gap(1.875em);
}

.wholesale-card{
  border: 1px solid #D4D4D4;
  &__top{
    padding: 1.13em 1.56em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__cart{
    display: flex;
    align-items: center;
  }
  &__btn{
    height: 2.8125em;
    padding: 0 1.75em;
    margin-left: 2.25em;
    span{
      font-size: 1em;
    }
  }
  &__bottom{
    border-top: 1px solid #D4D4D4;
    padding: 1.56em;
  }
  &__props{
    column-count: 2;
    column-gap: 4.06em;
  }
  .prop-item{
    @include lastM(0 0 .94em);
    display: inline-flex;
    width: 100%;
  }
}


@media screen and (max-width: 992px){
  .filter-categories2{
    .filter-categories__link{
      @include lastM(0 0 1em);
    }
  }

  .wholesale{
    gap: 1.25em;
  }

  .wholesale-card{
    &__top{
      padding: .94em;
    }
    &__bottom{
      padding: 1.56em .94em;
    }
    &__btn{
      margin-left: 1.19em;
    }
    .text25{
      font-size: 1.375em;
    }
    &__props{
      column-gap: 6.12em;
    }
  }
}


@media screen and (max-width: 576px){
  .wholesale-card{
    &__top{
      display: block;
    }
    &__cart{
      margin-top: .44em;
      justify-content: space-between;
    }
    &__props{
      column-count: 1;
    }
  }
}
