.circle-text{
  font-family: $font2;
  animation-name: rotate-circle;
  animation-duration: 10s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  font-size: .9375em;
  span{
    line-height: 110%;
    font-weight: 700;
    letter-spacing: 0.08438em;
    opacity: .75;
    &:first-child{
      color: $primary;
      padding: 0 .1em;
    }
  }
  &__wrap{
    position: relative;
  }
  &__icon{
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
    width: .6875em;
    height: auto;
  }
}

@keyframes rotate-circle {
  to {
    transform: rotate(1turn);
  }
}


.main-slider{
  overflow: hidden;
  position: relative;
  .swiper{
    &-container{
      @media screen and (min-width: 993px){
        height: auto !important;
      }
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-pagination{
      bottom: 5em;
      width: auto;
      padding: 0 6.125em;
      z-index: 3;
    }
  }
}

.main-slide{
  &__inner{
    display: grid;
    grid-template-columns: 52.06em calc(100% - 52.06em);
    min-height: 53.5625em;
  }
  &__info{
    padding: 5.75em 3em 1em 6.125em;
  }
  &__desc{
    margin-top: .5em;
    width: 30.5em;
    max-width: 100%;
    position: relative;
    .text21{
      color: #5A5A5A;
    }
  }
  .circle-text__wrap{
    margin-left: 5em;
    position: absolute;
    left: 100%;
    top: 50%;
    transform: translateY(-50%);
  }
  .banner{
    @media screen and (min-width: 577px){
      &._big{
        grid-row: 1/3;
        grid-column: 2/3;
      }
    }
  }
}

.banner{
  background: linear-gradient(159deg, #F6F6FA 46.4%, #DFDFE4 86.39%);
  position: relative;
  height: 100%;
  display: flex;
  align-items: flex-end;
  padding: 11.44em 6.19em;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    &:after{
      background: radial-gradient(29.69% 31.77% at 10.36% 63.59%, rgba(255, 255, 255, 0.60) 0%, rgba(255, 255, 255, 0.00) 100%);
    }
  }
  &__inner{
    position: relative;
    z-index: 2;
    max-width: 17em;
  }
  &__nav{
    margin-top: 1.75em;
    display: flex;
  }
  &__text{
    margin-top: .25em;
  }
  &__category{
    text-transform: uppercase;
    font-weight: 700;
    margin-bottom: .25em;
    .text12{
      letter-spacing: 0.06em;
    }
  }
  @media screen and (min-width: 577px){
    &._small{
      padding: 6.625em 6.125em;
      align-items: unset;
    }
  }
  &._small{
    background: linear-gradient(258deg, #F9F5F5 15.9%, #EFEEEE 73.69%), #D9D9D9;
    &:after{
      display: none;
    }
    .banner__img{
      left: unset;
      right: 0;
      top: unset;
      bottom: 0;
      width: 60%;
      height: unset;
      img{
        width: 100%;
        height: auto;
        object-fit: unset;
      }
    }
  }
}

.main-slide2,
.main-slide3{
  &__desc{
    margin-top: .94em;
    max-width: 30.5em;
  }
  .banner__nav{
    .m-btn{
      padding: 0 1.5em;
    }
  }
}

.main-slide2{
  &__inner{
    height: 100%;
    display: grid;
    grid-template-columns: calc(100% - 72.5em) 36.25em 36.25em;
    grid-template-rows: 50% 50%;
  }
  &__info{
    padding: 9.69em 1.875em 5em 6.125em;
  }
  &__info,
  .banner._big{
    @media screen and (min-width: 577px){
      grid-row: 1/3;
      .banner__img{
        &:after{
          background: linear-gradient(180deg, #FFF 0%, rgba(255, 255, 255, 0.00) 35.54%);
        }
      }
    }
  }
  .banner{
    display: block;
    padding: 3.062em 3.75em;
    &__inner{
      max-width: 22em;
    }
    &__img{
      &:after{
        background: linear-gradient(92deg, rgba(255, 255, 255, 0.84) 9.88%, rgba(255, 255, 255, 0.00) 93.46%);
      }
    }
  }
}

.main-slide3{
  padding: 10em 6.125em;
  background-size: cover;
  background-position: center;
  &:after{
    background: linear-gradient(268deg, rgba(255, 255, 255, 0.64) 5.34%, rgba(255, 255, 255, 0.00) 56.31%);
  }
  &__info{
    max-width: 34em;
  }
}

.main-slide4{
  &__inner{
    height: 100%;
    display: grid;
    grid-template-columns: calc((100% - 44.375em) / 2) 44.375em calc((100% - 44.375em) / 2);
  }
  .banner{
    display: block;
    padding: 7.5em 1em 10.38em;
    text-align: center;
    &:not(:last-child){
      border-right: 1px solid #E9E9E9;
    }
    &__inner{
      height: 100%;
      max-width: 28em;
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__nav{
      margin-top: auto;
      padding-top: 2em;
    }
  }
}


@media screen and (max-width: 992px){
  .circle-text {
    font-size: .75em;
  }

  .main-slider{
    .swiper-pagination{
      position: static;
      margin-top: 2.19em;
      width: 100%;
      justify-content: center;
    }
  }

  .main-slide{
    &__inner{
      min-height: 28.4375em;
      grid-template-columns: 23.38em calc(100% - 23.38em);
    }
    &__info{
      padding: 2.31em 1.56em;
    }
    &__desc{
      max-width: 17em;
    }
    .circle-text__wrap{
      top: 20%;
      margin-left: 1em;
    }
  }

  .banner{
    padding: 1.56em;
    .text34{
      font-size: 1.3125em;
      line-height: 114%;
    }
    &__nav{
      margin-top: .75em;
    }
    &__inner{
      max-width: 13em;
    }
    &__text{
      margin: 0;
    }
    &__category{
      margin-bottom: .5em;
      .text13{
        font-size: .75em;
      }
    }
    &._small{
      min-height: 13.9375em;
      padding: 1.56em;
      align-items: flex-end;
      .banner__img{
        width: 54%;
        bottom: unset;
        top: 1.125em;
      }
    }
  }

  .main-slide2,
  .main-slide3{
    .banner__nav{
      .m-btn{
        height: 3.8125em;
      }
    }
  }

  .main-slide2{
    &__inner{
      grid-template-columns: calc(100% - 30em) 15em 15em;
    }
    &__info{
      padding: 3.25em .25em 1.56em 1.56em;
    }
    &__desc{
      margin-top: .44em;
      .text21{
        letter-spacing: .02em;
      }
    }
    .banner{
      padding: 2.06em .875em;
      .text34{
        max-width: 90%;
      }
      &__nav{
        margin-top: .44em;
      }
    }
  }

  .main-slide3{
    padding: 6.69em 1.56em;
    &__info{
      max-width: 20em;
    }
    &__desc{
      margin-top: .25em;
    }
    .banner__nav{
      margin-top: 1.12em;
    }
  }

  .main-slide4{
    &__inner{
      grid-template-columns: repeat(3,calc(100% / 3));
    }
    .banner{
      padding: 1.56em;
      .text48{
        font-size: 1.4375em;
        line-height: 118%;
      }
      &__nav{
        .m-btn{
          padding: 0 1.44em;
          height: 2.75em;
          span{
            font-size: 1.0625em;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main-slider{
    .swiper-container{
      transition: all .3s;
      min-height: 55em;
    }
    .swiper-wrapper{
      align-items: unset;
    }
    .swiper-slide{
      height: 100%;
      &>div{
        height: 1px;
        overflow: hidden;
      }
      &-active{
        &>div{
          height: auto;
        }
      }
    }
  }

  .main-slide{
    &__inner{
      min-height: unset;
      grid-template-columns: 100%;
    }
    &__info{
      padding: 2.31em .94em;
    }
    &__desc{
      margin-top: .75em;
    }
    .banner{
      min-height: 20.3125em;
      padding: 1.25em .94em;
      &._small{
        min-height: 20.3125em;
      }
    }
  }

  .main-slide2{
    &__inner{
      display: block;
    }
    &__info{
      padding: .81em .25em .81em .94em;
      .text76{
        max-width: 95%;
      }
      .banner__nav{
        margin-top: 1.125em;
      }
    }
    &__desc{
      margin-top: .25em;
      .text21{
        font-size: .9275em;
      }
    }
    .banner{
      height: 15.625em;
      .text34{
        font-size: 1.4375em;
      }
    }
  }

  .main-slide3{
    padding: 9.06em .94em;
    background-size: 1920px auto;
    background-position: center top;
    &__info{
      max-width: 20em;
    }
    .banner__nav{
      margin-top: 1.12em;
    }
  }

  .main-slide4{
    &__inner{
      grid-template-columns: 100%;
    }
    .banner{
      height: 21.4375em;
    }
  }
}
