.advantages{
  padding: 3.81em 0;
  &__inner{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 4.1875em * 3 / 4));
    gap: 4.1875em;
  }
}

.advantage-item{
  display: flex;
  align-items: flex-start;
  &:before{
    content: '';
    flex-shrink: 0;
    margin: .31em 1.56em 0 0;
    width: 3.125em;
    height: 3.125em;
    background-color: $black;
    background-image: url(../img/check.svg);
    @include bgImgSize(1.0625em);
  }
  &__content{
    @include grid100gap(.625em);
  }
  .text24{
    max-width: 75%;
  }
}

.categories{
  padding-bottom: 1.5625em;
  overflow: hidden;
  &__nav{
    margin-top: 3.125em;
    @include center;
  }
}

.categories-slider{
  .swiper{
    &-container{
      width: calc(100% + 2px);
      margin-left: -1px;
    }
    &-slide{
      width: 25%;
      border-top: 1px solid #CECECE;
      border-bottom: 1px solid #CECECE;
      border-left: 1px solid #CECECE;
      &:last-child{
        border-right: 1px solid #CECECE;
      }
    }
    @media screen and (min-width: 577px){
      &-wrapper{
        transform: unset !important;
      }
      &-nav{
        display: none;
      }
    }
  }
}

.category-card{
  height: 23.3125em;
  padding: 4.63em 3.75em;
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    ._view2{
      display: none;
    }
  }
  &__content{
    position: relative;
    z-index: 2;
    @include grid100gap(.69em);
    max-width: 14.7em;
  }
  &__nav{
    display: flex;
    position: relative;
    z-index: 2;
    margin-top: 2.5em;
    .m-btn{
      padding: 0 2.25em;
      height: 3.375em;
    }
    ._view2{
      display: none;
    }
  }
}

.swiper-nav{
  display: flex;
  align-items: center;
  .swiper-button{
    position: static;
    width: 2.3125em;
    height: auto;
    margin: 0;
    @include lastM(0 1.25em 0 0);
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.buyers{
  padding: 3.81em 0 5.44em;
  border-bottom: 1px solid #CECECE;
  background: #FBFBFB;
  &__top{
    @include grid100gap(.75em);
    .text56{
      letter-spacing: -0.03em;
    }
    .text23{
      max-width: 24em;
    }
  }
  &__grid{
    margin-top: 3.75em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 11em / 3));
    gap: 5.5em;
  }
  &__nav{
    display: flex;
    margin-top: 3.75em;
    .m-btn{
      min-width: 17.3125em;
    }
  }
}

.buyer-item{
  display: flex;
  align-items: flex-start;
  &__icon{
    flex-shrink: 0;
    margin-right: 2.19em;
    img{
      max-height: 3.875em;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.81em);
  }
}

.categories2{
  padding: 1.4em 0 1.2em;
  .text56{
    letter-spacing: -.03em;
  }
  .categories-slider{
    margin-top: 3.125em;
    @media screen and (min-width: 993px){
      .swiper{
        &-slide{
          width: calc(100% / 3);
        }
      }
      .category-card__content{
        max-width: 21em;
        .text17{
          font-size: .9375em;
        }
      }
    }
  }
}

.sales{
  padding: 1.2em 0 2.8125em;
}

.product-slider{
  position: relative;
  margin-top: 3.375em;
  &__wrap{
    overflow: hidden;
  }
  &__container{
    width: calc(100% + 2px);
    margin-left: -1px;
  }
  &__wrapper{
    align-items: stretch;
  }
  &__slide{
    width: 25%;
    height: auto;
    &:not(:last-child){
      .product-card{
        border-right: none;
      }
    }
  }
  .swiper-nav{
    position: absolute;
    right: 6.125em;
    bottom: 100%;
    margin-bottom: 5em;
  }
}

.product-card{
  padding: 2.5em;
  border: 1px solid #D4D4D4;
  height: 100%;
  &__top{
    position: relative;
    padding-top: 1.69em;
  }
  &__slider{
    overflow: hidden;
    .swiper{
      &-pagination{
        margin-top: -.0625em;
        position: relative;
        z-index: 2;
        justify-content: center;
        &-bullet{
          background: #C7C7C7;
          width: 0.9375em;
          height: 0.125em;
          &:hover,
          &-active{
            background: $black;
          }
        }
      }
    }
  }
  &__img{
    display: block;
    @include imgCover(80%);
  }
  &__badges{
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .m-badge{
      @include lastM(0 0 .31em);
    }
  }
  .favourite-btn{
    position: absolute;
    top: .84em;
    right: 0;
    z-index: 2;
  }
  &__content{
    margin-top: 1.31em;
    @include grid100gap(.75em);
  }
  &__reviews{
    display: flex;
    align-items: center;
    .rating-block{
      margin-right: .625em;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__prices{
    padding: .19em 0;
  }
  &__sale{
    display: flex;
    align-items: center;
    margin-top: .375em;
    .m-badge{
      margin-left: .5em;
    }
  }
  &__price2{
    color: #C5C6C6;
    text-decoration: line-through;
  }
  &__btn{
    padding: 0 1.75em;
    height: 2.8125em;
    span{
      font-size: 1em;
    }
  }
}

.m-badge{
  border: 1px solid #A8A8A8;
  height: 1.3125em;
  display: flex;
  align-items: center;
  padding: 0 .5em;
  text-transform: uppercase;
  font-weight: 700;
  span{
    letter-spacing: 0.03438em;
    font-size: .6875em;
    line-height: 119%;
  }
  &._orange{
    color: #F59E2D;
    border-color: #F59E2D;
  }
  &._red{
    border-color: rgba($primary,.4);
    color: $primary;
  }
}

.cart-btn,
.favourite-btn{
  position: relative;
  @include center;
  &:after{
    content: '';
    @include centerAbsolute;
    width: 100%;
    height: 100%;
    background-image: url(../img/check.svg);
    @include bgImgSize(40%);
    transition: inherit;
    opacity: 0;
  }
  &._added{
    &:after{
      opacity: 1;
    }
  }
}

.favourite-btn{
  width: 1.625em;
  svg{
    width: 100%;
    height: auto;
  }
  &._added{
    path{
      stroke: #4C4C4C;
      fill: #4C4C4C;
    }
  }
}

.color-field{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  min-height: 1.625em;
  &__item{
    margin: 0 .5em 0 0;
    width: 1.0625em;
    height: 0.75em;
    cursor: pointer;
    &:before{
      content: '';
      position: absolute;
      top: -.1875em;
      left: -.1875em;
      width: calc(100% + .375em);
      height: calc(100% + .375em);
      border: 1px solid $darkGray;
      transition: all .2s;
      opacity: 0;
    }
    &._active:before{
      opacity: 1;
    }
  }
}

.bestseller{
  padding: 2.75em 0 1.4em;
  .tabs{
    margin-top: 1.25em;
    padding: 0 6.125em;
  }
  &__content{
    margin-top: 2.31em;
  }
  .swiper-nav{
    margin-bottom: 8.125em;
  }
}

.tabs{
  display: flex;
  flex-wrap: wrap;
  .tab{
    margin: 1.25em 2.5em 0 0;
  }
}

.tab{
  text-transform: uppercase;
  color: #C5C6C6;
  font-weight: 700;
  padding-bottom: .44em;
  border-bottom: 2px solid transparent;
  transition: all .3s;
  cursor: pointer;
  span{
    font-size: 1em;
    line-height: 125%;
  }
  &:hover{
    color: $black;
  }
  &._active{
    pointer-events: none;
    color: $black;
    border-color: $black;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.new-collection{
  padding: 1.5625em 0 0;
  @media screen and (min-width: 993px){
    .swiper-nav{
      display: none;
    }
    .product-slider__wrapper{
      transform: unset !important;
      display: grid;
      grid-template-columns: calc(100% - 78.25em) 39.125em 39.125em;
    }
    .product-slider__slide{
      width: 100% !important;
      &:nth-child(5)~.product-slider__slide{
        display: none;
      }
      &:nth-child(1){
        grid-row: 1/3;
        .product-card{
          padding: 2.75em 3.75em;
        }
      }
      &:not(:first-child){
        .product-card{
          padding: 2.28em 1.5em;
          display: grid;
          grid-template-columns: 13.4375em calc(100% - 13.4375em - 2.22em);
          align-items: center;
          justify-content: space-between;
          &__slider{
            height: 100%;
            display: flex;
            flex-direction: column;
            .swiper-container{
              height: 100%;
            }
            .swiper-slide{
              display: flex;
              align-items: center;
            }
          }
          &__img{
            width: 100%;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .advantages{
    padding: 3.21em 0;
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }

  .advantage-item{
    &:before{
      width: 1.875em;
      height: 1.875em;
      margin: 0 1.25em 0 0;
    }
    &__content{
      gap: .44em;
    }
    .text24{
      font-size: 1.25em;
      line-height: 120%;
      max-width: 100%;
    }
    .text16{
      font-size: 0.9375em;
      line-height: 140%;
    }
  }

  .categories{
    &__nav{
      margin-top: 1.625em;
      .m-btn{
        padding: 0 1.31em;
        height: 3.6875em;
        span{
          font-size: .9375em;
        }
      }
    }
  }

  .categories-slider{
    .swiper-slide{
      width: calc(100% / 3);
    }
  }

  .category-card{
    padding: 2.69em .3125em;
    text-align: center;
    &__img{
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
    }
    &__content{
      gap: .44em;
      max-width: 100%;
    }
    .text34{
      font-size: 1.3125em;
      line-height: 114%;
    }
    .text17{
      font-size: .875em;
      line-height: 157%;
      @include truncate(2);
    }
    &__nav{
      margin-top: .75em;
      justify-content: center;
      .m-btn{
        height: 3.25em;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .buyers{
    padding: 3.125em 0;
    &__top{
      gap: .56em;
      .text23{
        max-width: 100%;
      }
    }
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &__nav{
      margin-top: 1.25em;
      justify-content: center;
      .m-btn{
        height: 3.6875em;
        min-width: 12.875em;
        span{
          font-size: 1em;
        }
      }
    }
  }

  .buyer-item{
    &__icon{
      margin-right: .94em;
      width: 1.9375em;
      img{
        width: 100%;
        max-height: unset;
      }
    }
    &__content{
      gap: .31em;
    }
    .text22{
      font-size: 1.125em;
      line-height: 128%;
      br{
        display: none;
      }
    }
    .text17{
      font-size: .9375em;
      line-height: 140%;
    }
  }

  .categories2{
    padding: 1.5625em 0;
    .categories-slider{
      margin-top: 1.25em;
    }
  }

  .swiper-nav{
    .swiper-button{
      width: 1.625em;
    }
  }

  .sales{
    padding: 1.5625em 0;
  }

  .product-slider{
    margin-top: 1.25em;
    .swiper-nav{
      margin-bottom: 1.875em;
      right: 1.5625em;
    }
  }

  .product-card{
    padding: 1em;
    .text23{
      line-height: 111%;
    }
    &__top{
      padding: 0;
    }
    &__badges{
      display: none;
    }
    .favourite-btn{
      top: 0;
      right: -.25em;
    }
    &__img{
      padding-bottom: 65.8%;
    }
    &__slider{
      .swiper-pagination{
        display: none;
      }
    }
    &__reviews{
      &-count{
        span{
          display: none;
        }
      }
      .rating-block{
        margin-right: .26em;
      }
    }
    &__content{
      gap: .5em;
    }
    &__bottom{
      @include grid100gap(.625em);
    }
    &__sale{
      margin-top: 0;
    }
  }

  .favourite-btn{
    width: 1.1875em;
  }

  .bestseller{
    padding: 2.5em 0 1.5625em;
    .tabs{
      padding: 0 1.5625em;
      margin-top: 1.25em;
    }
    &__content{
      margin-top: 1.25em;
    }
    .swiper-nav{
      margin-bottom: 4.625em;
    }
  }

  .tabs{
    flex-wrap: nowrap;
    overflow-x: auto;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
    .tab{
      flex-shrink: 0;
      margin: 0 1.6875em 0 0;
    }
  }

  .tab{
    padding-bottom: .25em;
    span{
      font-size: .8125em;
      line-height: 154%;
    }
  }
}


@media screen and (max-width: 576px) {
  .advantages {
    padding: 2.5em 0;
    &__inner {
      grid-template-columns: 100%;
    }
  }

  .categories{
    &__nav{
      padding: 0 .94em;
      margin-top: 1.5em;
      justify-content: space-between;
    }
  }

  .categories-slider{
    .swiper{
      &-slide{
        width: 16em;
      }
      &-nav{
        display: flex;
        align-items: center;
      }
    }
  }

  .buyers{
    padding: 2.81em 0;
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }

  .categories2{
    .swiper-nav{
      justify-content: center;
      margin-top: 1.25em;
    }
  }

  .categories2{
    padding: 1.25em 0;
  }

  .product-slider{
    .swiper-nav{
      right: .94em;
    }
    &__slide{
      width: 50%;
    }
  }

  .product-card{
    &__img{
      width: calc(100% + .75em);
      margin-left: -.375em;
    }
  }

  .bestseller{
    .tabs{
      padding: 0 .9375em;
    }
  }

  .tabs{
    margin: 0 1.4375em 0 0;
  }
}
