.hit{
  padding: 2.81em 0 0;
  &__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 2.12em;
  }
  &__info{
    @include grid100gap(1.06em);
    max-width: 39em;
  }
  &__link{
    white-space: nowrap;
    margin-left: 1em;
  }
}

.room{
  position: relative;
  z-index: 2;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__bg{
    display: none;
  }
}

.room-item{
  position: absolute;
  &__point{
    z-index: 2;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    background: $primary;
    box-shadow: 0 .25em 1.0625em 0 $primary;
    position: relative;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      &:before{
        border-color: #fff;
      }
    }
    &:before{
      z-index: -1;
      content: '';
      @include centerAbsolute;
      width: 2.25em;
      height: 2.25em;
      border: 1px solid rgba(#FFF,.6);
      border-radius: 50%;
    }
  }
  &._show{
    z-index: 2;
    @media screen and (min-width: 577px){
      .product-card3{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &._reverse{
    @media screen and (min-width: 577px){
      .product-card3{
        transform: translateX(100%);
      }
    }
  }
}

.product-card3{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  bottom: .5em;
  right: .5em;
  padding: .625em .625em 1.31em;
  background: #FFF;
  box-shadow:  0 12px 27px 0px rgba(0, 0, 0, 0.15), 0px 49px 49px 0px rgba(0, 0, 0, 0.13), 0px 109px 66px 0px rgba(0, 0, 0, 0.08), 0px 194px 78px 0px rgba(0, 0, 0, 0.02), 0px 304px 85px 0px rgba(0, 0, 0, 0.00);
  width: 19.5625em;
  &__top{
    position: relative;
  }
  &__img{
    @include imgCover(100%);
  }
  .color-field{
    position: absolute;
    left: 50%;
    bottom: .87em;
    transform: translateX(-50%);
  }
  &__bottom{
    padding: 1em .5em 0;
    @include grid100gap(.94em);
  }
  &__cart{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__buttons,
  &__prices{
    display: flex;
    align-items: center;
  }
  &__price1{
    margin-right: .625em;
  }
  &__price2{
    text-decoration: line-through;
    color: #C5C6C6;
  }
  &__btn{
    border: 1px solid #ECECEC;
    width: 2.4375em;
    height: 2.5625em;
    margin-left: .625em;
  }
  .cart-btn{
    &:not(._added){
      background-color: transparent;
    }
    &:hover{
      background-color: $primary;
      path{
        stroke: #fff;
      }
    }
    svg{
      width: 1.1875em;
    }
  }
  .favourite-btn{
    svg{
      width: .8125em;
    }
    &._added{
      &:after{
        display: none;
      }
    }
    &:hover{
      border-color: $primary;
    }
  }
}

.faq{
  padding: 4.75em 0 3.125em;
  border-top: 1px solid #E9E9E9;
  .text56{
    max-width: 10em;
  }
  &__items{
    padding: 3.75em 3.63em 0;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 5.875em));
    justify-content: space-between;
  }
}

.qa-card{
  border-bottom: 1px solid #F5F5F5;
  &._active{
    .qa-card__title{
      font-weight: 500;
    }
    .qa-card__btn svg{
      transform: rotate(-180deg);
    }
  }
  &__show{
    padding: .625em 0;
    min-height: 5.125em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:hover{
      cursor: pointer;
      color: #4D4D4D;
    }
  }
  &__btn{
    width: .625em;
    flex-shrink: 0;
    margin-left: 1em;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__hidden{
    padding: .625em .34em;
    border-top: 1px solid #F5F5F5;
    display: none;
  }
  &__text{
    font-size: .9375em;
    line-height: 167%;
    a{
      text-decoration: underline;
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .hit{
    padding: 1.5625em 0 0;
    &__top{
      margin-bottom: 1.87em;
    }
    &__info{
      gap: .75em;
    }
    &__link{
      height: 3.6875em;
      padding: 0 1.31em;
      span{
        font-size: 1em;
      }
    }
  }

  .room-item{
    &__point{
      width: .625em;
      height: .625em;
      &:before{
        width: 1.375em;
        height: 1.375em;
      }
    }
  }

  .product-card3{
    width: 11.9375em;
    padding: .375em .375em .66em;
    bottom: .3125em;
    right: .3125em;
    .color-field{
      flex-wrap: nowrap;
      bottom: .25em;
    }
    &__bottom{
      padding: .65em .31em 0;
      gap: .375em;
    }
    .text23{
      font-size: 1.125em;
      line-height: 111%;
    }
    &__prices{
      display: block;
      padding-top: .2em;
    }
    &__price1{
      margin: 0;
      .text20{
        font-size: 1.1875em;
        line-height: 105%;
      }
    }
    &__price2{
      .text20{
        font-size: 1em;
        line-height: 125%;
      }
    }
  }

  .faq{
    padding: 3.125em 0 1.5625em;
    .text56{
      max-width: 100%;
    }
    &__items{
      padding: .94em 0 0;
      grid-template-columns: 100%;
    }
  }

  .qa-card{
    &__show{
      min-height: 4.0625em;
    }
    &__title{
      font-size: 1.0625em;
    }
  }
}


@media screen and (max-width: 576px){
  .hit{
    padding: 1.25em 0 0;
    &__top{
      margin-bottom: 1.25em;
      flex-direction: column;
      align-items: flex-start;
    }
    &__link{
      margin: .75em 0 0;
    }
  }

  .room{
    &__bg{
      position: fixed;
      z-index: 4;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      opacity: 0.95;
      background: $black;
      //display: block;
    }
    &._show-bg{
      .room__bg{
        display: block;
      }
    }
  }

  .room-item{
    &._show-mob{
      .product-card3{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .product-card3{
    z-index: 5;
    position: fixed;
    top: 50%;
    left: 50%;
    bottom: unset;
    right: unset;
    transform: translate(-50%,-50%);
  }

  .faq{
    padding: 2.5em 0 1.875em;
  }

  .qa-card{
    &__show{
      min-height: 2.9375em;
    }
    &__title{
      font-size: 1.0625em;
      line-height: 158%;
    }
  }
}
