.page-top {
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 4.375em;
  }
  &__link {
    color: #575757;
    position: relative;
    text-transform: uppercase;
    &:not(:last-child) {
      margin-right: 5.625em;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -2.8125em;
        transform: translate(0, -50%);
        width: 0.0625em;
        height: 100%;
        background: #D9D9D9;
      }
    }
    &:before {
      content: '';
      position: absolute;
      left: -0.9375em;
      bottom: -1.25em;
      width: 0;
      height: 0.0625em;
      background: #E31E24;
      transition: all 0.5s;
    }
    &:hover {
      color: #282828;
      &:before {
        width: calc(100% + 1.875em);
      }
    }
    &--active {
      color: #282828;
      &:before {
        width: calc(100% + 1.875em);
      }
    }
  }
  &._about{
    padding-bottom: 0;
  }
}


.about-main {
  &__top {
    display: flex;
    justify-content: space-between;
    padding: 3.75em 9.375em;
  }
  &__title {
    max-width: 40.625em;
    margin-right: 2.5em;
    p {
      color: #838383;
    }
    span {
      color: #171717;
    }
  }
  &__text {
    max-width: 34.375em;
    display: grid;
    grid-template-columns: 100%;
    gap: 0.9375em;
  }
  &__bottom {
    color: #ffffff;
    background-image: url("../img/about-bg.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 8.4375em 0.9375em 5em;
  }
  &__bottom-wrap {
    max-width: 43.125em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
    text-align: center;
    margin: 0 auto;
  }
  &__marks {
    width: 4.625em;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
  &__bottom .about-main__text {
    max-width: 100%;
    margin: 0 auto;
    p {
      font-style: italic;
    }
  }
}

.advantages-about {
  padding: 3.75em 0;
  &__title {
    max-width: 40.625em;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 15em / 3));
    gap: 7.5em;
    margin-top: 3.75em;
  }
  &__item {
    display: flex;
    &-icon {
      flex-shrink: 0;
      max-width: 3.875em;
      img {
        width: auto;
        max-width: 100%;
      }
    }
    &-desc {
      margin-left: 2em;
    }
    &-text {
      margin-top: 0.625em;
    }
  }
}

.about-numbers {
  padding: 3.75em 0 1.875em;
  &__wrap {
    display: grid;
    grid-template-columns: repeat(2, calc(50%));
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 4.6875em / 2));
    gap: 4.6875em;
  }
  &__item {
    &-info span:nth-child(1) {
      font-size: 3.8125em;
      line-height: 100%;
    }
    &-info span:nth-child(2) {
      font-size: 1.875em;
      line-height: 100%;
    }
  }
  &__title {
    max-width: 34.6875em;
    margin-bottom: 3.75em;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__bottom {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 18.75em / 4));
    gap: 6.25em;
    margin-top: 4.6875em;
  }
  &__bottom .about-numbers__item-info {
    color: #E31E24;
  }
}

.mission {
  background: #FBFBFB;
  padding: 3.75em 0 6.25em;
  margin-top: 1.875em;
  &__top {
    max-width: 43.75em;
    display: grid;
    grid-template-columns: 100%;
    gap: 0.75em;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 6.25em;
    padding: 0 9.375em;
    margin-top: 3.75em;
  }
  &__item {
    display: flex;
    align-items: center;
    &-img {
      flex-shrink: 0;
      width: 34.0625em;
    }
    &-img img {
      width: 100%;
    }
    &-desc {
      display: grid;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 0.625em;
    }
  }
  &__item:nth-child(odd) {
    .mission__item-img {
      margin-right: 2.5em;
    }
  }
  &__item:nth-child(even) {
    .mission__item-img {
      margin-left: 2.5em;
    }
  }
}


@media screen and (max-width: 992px) {
  .text22-tablet {
    font-size: 1.375em;
  }

  .page-top {
    padding: 1.25em 0;
    &__list {
      margin-top: 1.875em;
    }
    &__link {
      font-size: 0.8125em;
      &:not(:last-child) {
        margin-right: 3.75em;
        &:after {
          right: -1.875em;
        }
      }
      &:before {
        bottom: -0.9375em;
      }
    }
  }

  .about-main {
    &__top {
      display: block;
      padding: 2.5em 0 4.375em;
    }
    &__title {
      margin: 0 0 0.625em 0;
    }
    &__bottom {
      padding: 3.75em 0.9375em 1.875em;
    }
    &__bottom-wrap {
      max-width: 27.1875em;
      gap: 1.625em;
    }
    &__marks {
      width: 2.8125em;
    }
    &__name {
      font-size: 1.375em;
    }
  }

  .advantages-about {
    padding: 2.5em 0 1.25em;
    &__title {
      max-width: 36.5625em;
    }
    &__items {
      grid-template-columns: 100%;
      gap: 1.25em;
      margin-top: 1.25em;
    }
    &__item {
      &-icon {
        max-width: 2.375em;
      }
      &-desc {
        margin-left: 0.9375em;
      }
    }
  }

  .about-numbers {
    padding: 1.25em 0;
    &__wrap {
      grid-template-columns: 100%;
    }
    &__group {
      padding: 0;
    }
    &__group:nth-child(1),
    &__group:nth-child(4){
      padding: 1.875em 1.25em !important;
    }
    &__group:nth-child(3) {
      order: 1;
    }
    &__title {
      margin-bottom: 1.25em;
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 4.25em / 2));
      gap: 4.25em;
    }
    &__bottom {
      grid-template-columns: repeat(4, calc(25% - 5.625em / 4));
      gap: 1.875em;
      margin-top: 1.875em;
    }
    &__bottom .about-numbers__item-info {
      span:nth-child(1) {
        font-size: 1.8125em;
      }
      span:nth-child(2) {
        font-size: 1.5em;
      }
    }
  }

  .mission {
    padding: 1.875em 0;
    margin-top: 1.25em;
    &__top {
      max-width: 34.375em;
    }
    &__items {
      gap: 1.875em;
      padding: 0;
      margin-top: 1.875em;
    }
    &__item {
      align-items: start;
      &-img {
        width: 15.3125em;
      }
    }
    &__item:nth-child(odd) {
      .mission__item-img {
        margin-right: 1.875em;
      }
    }
    &__item:nth-child(even) {
      .mission__item-img {
        margin-left: 1.875em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .page-top {
    &._about{
      margin-bottom: 11.875em;
      position: relative;
      .container{
        padding-bottom: 4.5em;
      }
    }
    &__list {
      margin-top: -2.75em;
      @include grid100gap(1.25em);
      text-align: left;
      align-items: unset;
      border: 0.0625em solid #E9E9E9;
      background: #FFF;
      padding: 0.9375em;
      width: calc(100% - 1.875em);
      position: absolute;
      top: 100%;
      left: .9375em;
    }
    &__link {
      line-height: 230%;
      &:not(:last-child) {
        margin: 0;
        &:after {
          display: none;
        }
      }
      &:before {
        display: none;
      }
      &--active {
        padding-left: 0.625em;
        &:before {
          display: block;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: 0.0625em;
          height: 100%;
        }
      }
    }
  }
  .about-main {
    &__top {
      padding: 0 0 3.75em;
    }
    &__bottom {
      padding: 4.6875em 0.9375em 3.125em;
    }
  }
  .about-numbers {
    &__items {
      grid-template-columns: 100%;
      gap: 1.5625em;
    }
    &__bottom {
      grid-template-columns: repeat(2, calc(50% - 0.9375em / 2));
      gap: 1.5em 0.9375em;
      margin-top: 1.25em;
    }
    &__bottom .about-numbers__item-info {
      span:nth-child(1) {
        font-size: 1.6875em;
      }
    }
  }
  .mission {
    &__items {
      gap: 2.5em;
      margin-top: 1.25em;
    }
    &__item {
      flex-direction: column;
      &-img {
        order: -1;
        width: 100%;
        margin: 0 0 1.25em 0 !important;
      }
      &-desc {
        gap: 0.625em;
      }
    }
  }
}
