.header{
  &__logo{
    width: 11.2em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
}

.header-top{
  background: #FAFAFA;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.close-btn{
  @include center;
  width: .75em;
  svg{
    width: 100%;
    height: auto;
  }
  &:hover{
    path{
      stroke: $primary;
    }
  }
}

.mob-menu{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-grow: 1;
  &__bg,
  &__close{
    display: none;
  }
}

.nav{
  display: flex;
  color: $dark;
  &__link,
  &__item{
    padding: 0 1.25em;
    &._sale{
      color: $primary;
      img{
        width: 1.25em;
        flex-shrink: 0;
        margin-right: .625em;
      }
    }
  }
  &__link,
  &__show{
    display: flex;
    align-items: center;
    height: 3.0625em;
    &:hover{
      color: $primary;
      cursor: pointer;
    }
    span{
      font-size: .9375em;
      line-height: 120%;
    }
    svg{
      width: .5625em;
      height: auto;
      margin-left: .375em;
    }
    &._black{
      color: $black;
    }
  }
  &__item{
    position: relative;
    &._show{
      .nav__hidden{
        opacity: 1;
        pointer-events: unset;
      }
      .nav__show svg{
        transform: rotate(-180deg);
      }
    }
  }
  &__hidden{
    @media screen and (min-width: 993px){
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      position: absolute;
      top: calc(100% - 2px);
      left: 0;
      padding-top: 2px;
      &-inner{
        background: #FBFBFB;
      }
    }
  }
  &__sublink{
    height: 3.125em;
    display: flex;
    align-items: center;
    padding: 0 1.25em;
    white-space: nowrap;
    &:hover{
      background: #F4F4F4;
      color: $dark;
    }
    span{
      font-size: .9375em;
      line-height: 120%;
    }
  }
}

.header-city{
  position: relative;
  &__show{
    height: 2.875em;
    display: flex;
    align-items: center;
    font-weight: 500;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      color: $primary;
      .header-city__arrow{
        path{
          fill: $primary;
        }
      }
      .header-city__icon{
        path{
          stroke: $primary;
          opacity: 1;
        }
      }
    }
  }
  &__icon{
    width: 1em;
    height: auto;
    margin-right: .75em;
  }
  &__arrow{
    width: .5625em;
    height: auto;
    margin-left: .375em;
  }
  &__hidden{
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .8125em;
    border: 1px solid #EEE;
    background: #FFF;
    padding: .94em 1.25em;
    box-shadow: $shadow;
    max-height: 21.8125em;
    overflow-y: auto;
    width: 44.875em;
    max-width: calc(100vw - 1.875em);
    @include scroll;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__items{
    column-count: 3;
    column-gap: 1.875em;
  }
  &__item{
    @include lastM(0 0 .6875em);
    text-decoration: underline;
    text-decoration-color: transparent;
    transition: all .3s;
    &:hover,
    &._active{
      color: $primary;
      text-decoration-color: $primary;
    }
    &:hover{
      cursor: pointer;
    }
  }
  &._show{
    .header-city__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.header-contacts{
  margin-left: 10em;
  display: flex;
  align-items: center;
  &__time{
    margin-left: .94em;
  }
  &__phone{
    margin-left: 1.44em;
    display: flex;
    align-items: center;
    svg{
      width: 1.375em;
      height: auto;
      margin-right: .94em;
      flex-shrink: 0;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.375em;
    height: 2.375em;
    @include center;
    @include lastM(0 .625em 0 0);
    border: 1px solid #E9E9E9;
    img{
      max-width: 1em;
      max-height: .875em;
      transition: all .3s;
    }
    &:hover{
      background: #F5F5F5;
      border-color: #ECECEC;
      img{
        opacity: 0.7;
      }
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    &._open-mob-menu{
      .mob-menu{
        transform: translateX(0);
        &__bg{
          display: block;
        }
        &__close{
          display: flex;
        }
      }
    }
    &._open-catalog-menu{
      .catalog-menu{
        transform: translateX(0);
        &__bg{
          display: block;
        }
        &__close{
          display: flex;
        }
      }
    }
    &__logo{
      width: 10em;
    }
  }

  .header-top{
    position: relative;
    z-index: 2;
    padding: .25em 0;
    .socials{
      &__item{
        border: none;
        width: 1em;
        height: auto;
        @include lastM(0 1.375em 0 0);
        img{
          width: 100%;
          max-height: unset;
          max-width: unset;
        }
      }
    }
  }

  .header-contacts{
    margin-left: unset;
    &__phone{
      margin-left: 1.375em;
      svg{
        display: none;
      }
      span{
        line-height: 188%;
      }
    }
    &__time{
      display: none;
    }
  }

  .burger{
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font-weight: 500;
    svg{
      width: 1.125em;
      height: auto;
      margin-right: .625em;
    }
  }

  .mob-menu{
    @include menu;
    display: block;
    padding-bottom: 2em;
  }

  .header-city{
    z-index: 3;
    &__show{
      justify-content: center;
    }
    &__hidden{
      width: calc(100% - 1em);
      left: .5em;
      margin-top: 0;
    }
    &__items{
      column-count: 1;
    }
  }

  .nav{
    flex-direction: column;
    &__show,
    &__link{
      height: 3.75em;
      justify-content: space-between;
      padding: 0 1.25em;
      &._sale{
        justify-content: unset;
      }
      span{
        font-size: 1.0625em;
      }
    }
    &__item,
    &__link{
      border-top: 1px solid #E9E9E9;
      &:last-child{
        border-bottom: 1px solid #E9E9E9;
      }
    }
    &__item{
      order: -1;
      padding: 0;
      &._show{
        .nav__show{
          background-color: $black;
          color: #fff;
          path{
            fill: #fff;
          }
        }
      }
    }
    &__hidden{
      padding: 1.25em 1.875em;
      display: none;
    }
    &__sublink{
      @include lastM(0 0 1.5em);
      padding: 0;
      height: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .header-top{
    .socials{
      &__item{
        @include lastM(0 1.06em 0 0);
      }
    }
  }

  .header-contacts{
    &__phone{
      margin-left: 1em;
    }
  }
}
