.page-top{
    padding: 2.875em 0;
    background-color: #FBF9F7;
    background-image: url(../img/bg.webp);
    background-size: cover;
    background-position: center;
    text-align: center;
    .breadcrumbs{
        margin-top: 1em;
        &:first-child{
            margin-top: 0;
        }
    }
    .search-field{
        margin: 2.69em auto 0;
        max-width: 100%;
        width: 62em;
    }
    &._small{
        padding: 1.25em 0;
    }
}

.breadcrumbs {
    &__item {
        color: $darkGray;
        position: relative;
        display: inline;
        &:last-child {
            color: #B0B0B0;
        }
        &:not(:first-child) {
            margin-left: 2.8125em;
            &:before {
                color: #DADADA;
                content: '';
                position: absolute;
                z-index: 1;
                font-size: 1.0625em;
                right: 100%;
                margin-right: .875em;
            }
        }
        span{
            font-size: 1.0625em;
            line-height: 118%;
        }
    }
}


@media screen and (max-width: 992px){
    .page-top{
        &:not(._small){
            padding: 0;
            .container{
                min-height: 11.0625em;
                padding-top: .94em;
                padding-bottom: .94em;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
            }
        }
        .search-field{
            margin-top: .625em;
        }
    }

    .breadcrumbs{
        &__item{
            span{
                font-size: .875em;
                line-height: 143%;
            }
            &:not(:first-child) {
                margin-left: 1.875em;
                &:before {
                    transform: translateY(.25em);
                    font-size: .875em;
                    margin-right: .5em;
                }
            }
        }
    }
}


