.header-bottom{
  border-top: 1px solid #E9E9E9;
  border-bottom: 1px solid #E9E9E9;
  &__inner{
    display: flex;
  }
  &__link{
    display: flex;
    align-items: center;
    padding: 0 5.38em;
    border-right: 1px solid #E9E9E9;
    border-left: 1px solid #E9E9E9;
    height: 3.3125em;
    text-transform: uppercase;
    svg{
      width: 1.125em;
      height: auto;
      margin-right: .75em;
      flex-shrink: 0;
    }
  }
}

.nav2{
  display: flex;
  .nav{
    @media screen and (min-width: 993px){
      &__link,
      &__item{
        padding: 0 2.38em;
      }
      &__link,
      &__show{
        height: 3.3125em;
        text-transform: uppercase;
        span{
          font-size: .875em;
        }
      }
      &__item{
        &._show{
          background: #FBFBFB;
        }
      }
      &__hidden{
        min-width: 14.8125em;
      }
    }

  }
}

.header-center{
  padding: 1.19em 0 1.875em;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
}

.m-search{
  position: relative;
  &__result{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    background-color: #fff;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    border-right: 1px solid #EDEDED;
    border-bottom: 1px solid #EDEDED;
    border-left: 1px solid #EDEDED;
  }
  &__items{
    @include grid100gap(.625em);
    @include scroll;
    overflow-y: auto;
    max-height: 25em;
  }
  &__link{
    @include center;
    height: 2.75em;
    font-weight: 700;
    color: $primary;
    border-top: 1px solid #DCDCDC;
    background: #FAFAFA;
    &:hover{
      background: darken(#FAFAFA,5%);
    }
  }
  &._show{
    z-index: 3;
    .m-search__result{
      opacity: 1;
      pointer-events: unset;
    }
    .search-field{
      input{
        border-color: $black;
      }
    }
  }
}

.mob-bottom-menu{
  max-width: 63.375em;
  flex-grow: 1;
  .m-search{
    width: 100%;
  }
}

.search-field{
  position: relative;
  &__btn{
    position: absolute;
    right: 1.25em;
    top: 50%;
    transform: translateY(-50%);
    @include center;
    width: 1.125em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  input{
    border: 1px solid #D7D7D7;
    width: 100%;
    padding: 0 3.5em 0 1.25em;
    height: 3.375em;
    font-size: 1em;
    line-height: 120%;
    font-family: $font1;
    &::placeholder{
      color: #C5C6C6;
    }
    &:focus{
      border-color: $black;
    }
  }
}

.search-item{
  min-height: 4.375em;
  padding: 1.25em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:hover{
    background: #F5F5F5;
    color: $black;
  }
  b{
    font-weight: 700;
  }
}

.header-action{
  display: flex;
  align-items: center;
  &__btn{
    display: flex;
    align-items: center;
    color: #383838;
    position: relative;
    @include lastM(0 1.44em 0 0);
    svg{
      width: 1.375em;
      flex-shrink: 0;
      height: auto;
      margin-right: .5em;
    }
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    &._cart{
      svg{
        width: 1.1875em;
      }
      ._pin{
        left: .56em;
      }
    }
  }
}

._pin{
  position: absolute;
  top: -.68em;
  left: .94em;
  min-width: 1.125em;
  height: 1.125em;
  padding: 0 .125em;
  @include center;
  background-color: $primary;
  border: 2px solid #FAFAFA;
  border-radius: 2.5em;
  font-weight: 700;
  color: #fff;
}

.catalog-menu__bg,
.catalog-menu__close,
.catalog-btn{
  display: none;
}


@media screen and (max-width: 992px){
  .header-bottom{
    border: none;
    &__link{
      display: none;
    }
  }

  .catalog-menu{
    @include menu;
  }

  .header-center{
    position: relative;
    padding: 1.25em 0 4.5625em;
  }

  .mob-bottom-menu{
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    .m-search{
      transition: all .3s;
      &._show{
        width: 100vw;
        margin-left: -14em;
      }
    }
    .search-field{
      input{
        border-color: #ECECEC;
        padding-left: 1.12em;
        height: 3.3125em;
      }
      &__btn{
        right: 1.56em;
      }
    }
  }

  .catalog-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 1px solid #ECECEC;
    border-bottom: 1px solid #ECECEC;
    height: 3.3125em;
    width: 14em;
    flex-shrink: 0;
    padding: 0 1em;
    text-transform: uppercase;
    white-space: nowrap;
    font-weight: 500;
    color: $black;
    &__icon{
      margin-right: .75em;
      width: 1.125em;
      height: auto;
    }
    &__arrow{
      width: .5625em;
      height: auto;
      margin-left: .375em;
    }
  }

  .nav2{
    display: block;
  }

  .catalog-menu{
    padding: 3.125em 0;
  }

  .header-action{
    &__btn{
      @include lastM(0 2.62em 0 0);
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-btn{
    width: 10.5em;
    padding: 0 .5em;
  }

  .mob-bottom-menu {
    .m-search {
      &._show {
        margin-left: -10.5em;
      }
    }
  }

  .m-search{
    &__items{
      gap: 0;
      max-height: calc(100 * var(--vh) - 13.875em);
    }
  }

  .search-item{
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    min-height: 6.3125em;
    &__btn{
      margin-top: .75em;
    }
  }

  .header-action{
    &__btn{
      @include lastM(0 1.625em 0 0);
      .text13{
        display: none;
      }
      svg{
        margin: 0;
      }
    }
  }
}
