.search{
  @media screen and (min-width: 993px){
    .catalog__items{
      grid-template-columns: repeat(4,25%);
      .catalog-item{
        margin-top: -1px;
        margin-left: -1px;
        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3),
        &:nth-child(4){
          margin-top: 0;
        }
        &:nth-child(4n-3){
          margin-left: 0;
        }
      }
    }
  }
}

.search-empty{
  padding: 9em 0;
  text-align: center;
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__info{
    @include grid100gap(1.13em);
  }
  &__text{
    line-height: 153%;
    color: #616161;
  }
  &__nav{
    margin-top: 2.75em;
    display: flex;
    justify-content: center;
  }
}

.subscribe{
  margin-top: auto;
  padding: 3.125em 0 5em;
  &__inner{
    padding: 2.56em 4.69em;
    background: #FBFBFB;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__info{
    @include grid100gap(.875em)
  }
}

.subscribe-socials{
  display: flex;
  align-items: center;
  .socials{
    margin: 0 0 0 .625em;
  }
}

.subscribe-form{
  max-width: 34.25em;
  flex-grow: 1;
  &._error{
    .fg{
      input{
        border-color: $primary;
      }
    }
    .arrow-btn{
      opacity: .4;
      pointer-events: none;
    }
  }
  &__field{
    position: relative;
  }
  .arrow-btn{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    color: $black;
    span{
      font-size: 1.0625em;
    }
    &:hover{
      span{
        text-decoration-color: $black;
      }
    }
  }
  .m-check{
    margin-top: .81em;
    color: #515151;
    label{
      font-size: .8125em;
    }
  }
  &__error{
    margin-top: .5em;
    color: $primary;
    span{
      font-size: .75em;
      line-height: 150%;
    }
  }
  &__success{
    min-height: 3.75em;
    display: flex;
    align-items: center;
    padding: .625em;
    border-bottom: 1px solid #E3E3E3;
    color: #333;
    font-weight: 500;
    svg{
      width: 1.0625em;
      margin-right: .625em;
      flex-shrink: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .search-empty{
    padding: 2.5em 0;
    &__info{
      gap: .94em;
    }
    &__nav{
      margin-top: 1.94em;
    }
    .text34{
      font-size: 1.4375em;
      line-height: 140%;
    }
    &__text{
      line-height: 140%;
    }
  }

  .subscribe{
    padding: 1.5625em 0 4em;
    &__inner{
      flex-wrap: wrap;
      padding: 2.19em 1.56em;
      align-items: flex-start;
    }
    &__info{
      width: 100%;
      text-align: center;
      gap: .31em;
      margin-bottom: 1.19em;
    }
    .text31{
      font-size: 1.1875em;
      line-height: 132%;
    }
    .text20{
      font-size: 1em;
      line-height: 150%;
    }
  }

  .subscribe-form{
    max-width: 27em;
  }

  .subscribe-socials{
    flex-direction: column;
    .socials{
      margin: .44em 0 0;
    }
  }
}


@media screen and (max-width: 576px){
  .subscribe{
    &__inner{
      padding: 1.56em;
      @include grid100gap(1.56em);
    }
    &__info{
      padding: 0;
    }
    .m-check{
      margin-top: 1.56em;
      label{
        font-size: .6875em;
      }
    }
  }
}
