@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 0%, #000 100%);
    pointer-events: none;
  }
}

.swiper-pagination{
  display: flex;
  &-bullet{
    background: #B7B6B6;
    opacity: 1;
    width: 2.1875em;
    height: 0.125em;
    margin: 0 .44em !important;
    position: relative;
    &:before{
      content: '';
      width: 100%;
      height: 1em;
      @include centerAbsolute;
    }
    &-active{
      background-color: $primary;
    }
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}
